* {
    box-sizing: border-box
}

body {
    min-width: 320px;
    font-family: 'Arial', 'Helvetica', sans-serif;
    font-size: 14px;
    line-height: 1.4;
    color: #000;
    background-color: #fff
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td,
figure {
    margin: 0;
    padding: 0
}

table {
    border-collapse: collapse;
    border-spacing: 0
}

fieldset,
img,
abbr {
    border: 0
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
    font-style: normal;
    font-weight: 400
}

ul li {
    list-style: none
}

caption,
th {
    text-align: left
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    font-size: 100%;
    // font-weight: 400
}

b,
strong {
    font-weight: 700
}

small {
    font-size: 80%
}

sup {
    vertical-align: text-top
}

sub {
    vertical-align: text-bottom
}

button,
.btn,
input,
optgroup,
select,
textarea {
    color: inherit;
    font: inherit;
    margin: 0
}

legend {
    color: #000
}

article,
aside,
blockquote,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
    display: block
}

time {
    display: inline-block
}

ul,
ol,
li {
    list-style: none
}

a {
    text-decoration: none;
    cursor: pointer
}

iframe {
    outline: 0;
    border: 0;
}

a:hover,
a:focus,
a:visited {
    text-decoration: none;
    outline: none
}

button,
.btn {
    display: inline-block;
    overflow: visible;
    background-color: transparent;
    border: none;
    outline: 0;
    text-align: center
}

button:focus,
.btn:focus,
button:active,
.btn:active {
    border: unset
}

button,
.btn,
select {
    text-transform: none
}

button,
.btn,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
    cursor: pointer
}

button[disabled],
[disabled].btn,
html input[disabled] {
    cursor: default
}

button::-moz-focus-inner,
.btn::-moz-focus-inner,
input::-moz-focus-inner {
    border: 0;
    padding: 0
}

input {
    border: 0;
    outline: 0;
    line-height: normal
}

input[type="checkbox"],
input[type="radio"] {
    box-sizing: border-box;
    padding: 0
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
    height: auto
}

input[type="search"] {
    -webkit-appearance: textfield;
    box-sizing: content-box
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none
}

input[type="text"]::-webkit-autofill {
    background-color: #fff !important
}

img {
    display: inline-block;
    border: 0;
    width: auto;
    height: auto;
    max-width: 100%
}

span {
    display: inline-block
}

.btn {
    transition: all.3s
}

.btn:visited,
a:visited {
    color: inherit
}
